.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.shell{
  background-color: paleturquoise;
  width: 400px;
  height: 600px;
  border-radius: 10px;
  margin: auto;
  margin-top: 15px;
}

.window{
  background-color: black;
  width: 360px;
  height: 230px;
  border-radius: 5px;
  margin: auto;
  margin-top: 20px;
  padding-top: 40px;
}

.windowmain{
  background-color: white;
  width: 280px;
  height: 190px;
  border-radius: 2px;
  margin: auto;
  margin-top: 0px;

}

.content{
  color: black;
  margin: auto;
  padding-top: 20px;
}

.buttons{
  /* background-color: yellow; */


